/**
 * Page
 *  - for page.html.twig and region markup.
 ============================================================================ */
.regions {
  border: 0.625em solid;
  border-bottom: 0;
}

.pr-messages__rw {
  border: 0;
}

.pr-leaderboard .regions {
  border-width: 5px 1.25em;
}

.pr-leaderboard .block:not(.has-title) {
  margin-top: 0;
  margin-bottom: 0;
}

.pr-leaderboard .block:not(.has-title) p {
  margin: 1rem 0 0;
}

.pr-leaderboard .block ul,
.pr-leaderboard .block ol {
  margin: 0;
}

@media all and (max-width: 45em) {
  .pr-leaderboard .block {
    float: none;
    margin: 0 auto;
  }
}

.pr-leaderboard__leaderboard {
  display: block;
}

.pr-leaderboard__leaderboard .menu.odd {
  float: none;
}

@media all and (min-width: 60.063em) {
  .pr-leaderboard__leaderboard .menu.odd {
    float: right;
  }
}

.pr-header {
  position: relative;
}

.pr-header .regions {
  border-width: 1.25em;
}

.pr-header .block {
  margin: 1rem 0;
}

.pr-header__header-second nav.rm-block {
  position: absolute;
  top: 50%;
  right: 0;
  z-index: 480;
}

@media all and (min-width: 60.063em) {
  .pr-header__header-second nav.rm-block {
    position: relative;
    top: auto;
    padding: 1rem 0;
  }
}

.pr-navbar .regions {
  border-width: 1.25em;
}

.pr-navbar__rw {
  padding: 0;
}

@media all and (min-width: 60.063em) {
  .pr-navbar__rw:not(.rm-row) .pr-navbar__navbar {
    display: table;
    width: 100%;
  }
  .pr-navbar__rw:not(.rm-row) .pr-navbar__navbar .block {
    display: table-cell;
    vertical-align: middle;
  }
}

.pr-navbar .block {
  margin: 0;
}

.pr-navbar .block ul {
  margin: 0;
}

.l-leaderboard + .l-header .regions,
.l-leaderboard + .l-navbar .regions {
  border-top: 0;
}

.l-header + .l-navbar .regions {
  border-bottom-width: 0.9375em;
  border-bottom-style: solid;
}

#navbar.headroom--not-top {
  position: fixed;
  top: 0;
  z-index: 450;
  width: 100%;
}

#navbar.headroom--not-top .regions {
  border-bottom: 0.625em solid;
}

.pr-highlighted__rw {
  border-top: none;
}

.pr-features + .pr-main .block,
.pr-features + .pr-content-prefix .block {
  margin-top: 0;
}

.pr-features .block {
  margin-top: 0;
  margin-bottom: 1.25rem;
}

.bp--at-core-simple-wide .pr-features .block {
  margin-bottom: 0;
}

.pr-features .block p:last-of-type {
  margin-bottom: 0;
}

.pr-main__sidebar-first .block__title, .pr-main__sidebar-second .block__title {
  margin: 0;
  border-radius: 0.125em 0.125em 0 0;
}

.pr-main__sidebar-first .block__content, .pr-main__sidebar-second .block__content {
  border-radius: 0 0 0.125em 0.125em;
}

.pr-main__sidebar-first .block__title,
.pr-main__sidebar-first .block__content, .pr-main__sidebar-second .block__title,
.pr-main__sidebar-second .block__content {
  padding: 0.75rem;
}

.pr-main__sidebar-first .block-search .block__content, .pr-main__sidebar-second .block-search .block__content {
  padding: 0;
}

.pr-subfeatures__rw {
  border: 0;
}

.pr-footercols__rw {
  border: 0;
}

.pr-footer__rw {
  border: 0;
  padding-top: 1.625rem;
  padding-bottom: 1.625rem;
}

.pr-footer__footer {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

.pr-footer__footer-first .block__inner {
  margin-right: 2rem;
  padding-bottom: 1.625rem;
}

.pr-footer__footer-first .block {
  margin-bottom: -1.625rem;
}

.pr-footer__footer-first .block:last-of-type {
  margin-bottom: 1.625rem;
}

.pr-footer__footer-first .block:last-of-type .block__inner {
  padding-bottom: 0;
}

.pr-footer__footer-first .block .field {
  margin-bottom: 0;
}

.pr-footer__footer-second .menu {
  column-count: 2;
}

.pr-footercols + .pr-footer .block {
  margin-top: 0;
}

.pr-disclaimer__rw {
  border: 0;
}

